
import React, { useEffect, useState,useRef,audioRef } from "react";
import { AllSongList } from "./Data";
import SoundWave from "../Assets/SongsList/SoundWave.gif"
import {motion} from "framer-motion"



const AllSongsList = () => {
  useEffect(()=>{
      window.scrollTo(0,0)
  },[])
    const [audioPlaying, setAudioPlaying] = useState(Array(AllSongList.length).fill(false));
  const audioRefs = useRef(Array(AllSongList.length).fill(null));
  
  const playAudio = (index) => {
    const newAudioPlaying = Array(AllSongList.length).fill(false);

    if (audioRefs.current[index]) { 
      audioRefs.current.forEach((ref, i) => {
        if (i === index) {
          if (audioPlaying[index]) {
            ref.pause();
          } else {
            ref.play();
            newAudioPlaying[i] = true;
          }
        } else {
          ref.pause();
        }
      });

      setAudioPlaying(newAudioPlaying);
    }
  };
  return (
    <motion.div className='HomePage' intial={{opacity : 0}} animate={{opacity : 1}} exit ={{opacity : 0,transition: {duration:0.05}}}>
        <div className='container py-4'>
      <h3 className='text-white my-3'>All Movies Song</h3>
      <div className="row row-cols-1 row-cols-sm-2 row-cols-md-3 row-cols-lg-4">
          {AllSongList.map((item, index) => (
            <div className="col d-flex justify-content-center my-2" key={index}>
              <div id="SmallCard">
                <div
                  className="img"
                  id="SmallCardImg"
                  style={{
                    background: `url(${item.ImageUrl})`,
                    backgroundRepeat: "no-repeat",
                    backgroundPosition: "center",
                    backgroundSize: "100% 100%",
                  }}
                >  {audioPlaying[index] && <img src={SoundWave} alt="SoundWave" style={{ width: '50px', height: '50px',backgroundColor:'#060505C7',borderRadius:'10px' }} />}
                </div>
                <div className="textBox">
                  <div className="textContent">
                    <p className="h1 my-1"> {item.Name} </p>
                  </div>
                  <p className="p my-1"> {item.MovieSong}
                  <div style={{display:'flex',justifyContent:'flex-end'}}>           
                    <div onClick={() => playAudio(index)} style={{ display: 'flex', justifyContent: 'flex-end' }}>
                      {audioPlaying[index] ? (
                        <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="currentColor" class="bi bi-pause-circle-fill" viewBox="0 0 16 16">
                        <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0M6.25 5C5.56 5 5 5.56 5 6.25v3.5a1.25 1.25 0 1 0 2.5 0v-3.5C7.5 5.56 6.94 5 6.25 5m3.5 0c-.69 0-1.25.56-1.25 1.25v3.5a1.25 1.25 0 1 0 2.5 0v-3.5C11 5.56 10.44 5 9.75 5"/>
                      </svg>
                      ) : (
                        <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="currentColor" className="bi bi-play-circle-fill" viewBox="0 0 16 16">
                          <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0M6.79 5.093A.5.5 0 0 0 6 5.5v5a.5.5 0 0 0 .79.407l3.5-2.5a.5.5 0 0 0 0-.814z" />
                        </svg>
                      )}
         
                    </div>
                  {/*<svg xmlns="http://www.w3.org/2000/svg" width="19" height="19" fill="currentColor" class="bi bi-heart mx-2" viewBox="0 0 16 16">
  <path d="m8 2.748-.717-.737C5.6.281 2.514.878 1.4 3.053c-.523 1.023-.641 2.5.314 4.385.92 1.815 2.834 3.989 6.286 6.357 3.452-2.368 5.365-4.542 6.286-6.357.955-1.886.838-3.362.314-4.385C13.486.878 10.4.28 8.717 2.01zM8 15C-7.333 4.868 3.279-3.04 7.824 1.143q.09.083.176.171a3 3 0 0 1 .176-.17C12.72-3.042 23.333 4.867 8 15"/>
</svg>
<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-send" viewBox="0 0 16 16">
  <path d="M15.854.146a.5.5 0 0 1 .11.54l-5.819 14.547a.75.75 0 0 1-1.329.124l-3.178-4.995L.643 7.184a.75.75 0 0 1 .124-1.33L15.314.037a.5.5 0 0 1 .54.11ZM6.636 10.07l2.761 4.338L14.13 2.576zm6.787-8.201L1.591 6.602l4.339 2.76z"/>
</svg>*/ }  
                    </div>
                    
                    
                    <audio ref={(ref) => (audioRefs.current[index] = ref)} src={item.song} />
                  </p>
                  <div></div>
                </div>
              </div>
            </div>
          ))}
        </div>
        
      </div>
    </motion.div>
  )
}

export default AllSongsList
